import React, { Component } from "react";
import {
   Row,
   Col,
   Form,
   Input,
   FormGroup,
   FormFeedback,
   Button,
   Label,
   Card,
   CardHeader,
   CardBody
} from "reactstrap";
import { logger } from "../../../Helpers/Logger";
import Validator from "js-object-validation";
import { ProfileValidations, ProfileValidationsMessaages } from "../../../validations/profile.js";
import Loader from "../../../containers/Loader/Loader";

class GenralSettings extends Component {
   constructor(props) {
      super(props);
      this.state = {
         errors: {},
         firstName: "",
         lastName: "",
         email: "",
         roleType:"admin"
      };
   }

   componentDidMount = () => {
      if (this.props.profileData && this.props.profileData.profileInfo) {
         const {
            firstName,
            lastName,
            email,
         } = this.props.profileData.profileInfo;
         this.setState({
            firstName: firstName || "",
            lastName: lastName || "",
            email: email || "",
            errors: {}
         });
      }
   }
   componentDidUpdate = ({ profileData }) => {
      if (this.props.profileData && this.props.profileData.profileInfo && profileData.profileInfo !== this.props.profileData.profileInfo) {
         const {
            firstName,
            lastName,
            email,
         } = this.props.profileData.profileInfo;
         this.setState({
            firstName: firstName || "",
            lastName: lastName || "",
            email: email || "",
            errors: {}
         });
      }
   }

   handleInputChange = e => {
      const { target } = e;
      const { name, value } = target;
      this.setState({
         [name]: value,
         errors: {
            ...this.state.errors,
            [name]: null
         }
      });

   };

   handleSubmit = e => {
      e.preventDefault();
      try {
         const {
            firstName,
            lastName,
            // email,
            roleType
         } = this.state;
         const payload = {
            firstName:firstName.trim(),
            lastName:lastName.trim(),
            // email:email.trim(),
            roleType
         };
         const { isValid, errors } = Validator(
            payload,
            ProfileValidations,
            ProfileValidationsMessaages
         );
         if (!isValid) {
            this.setState({
               errors
            });
            return;
         }
         else {
            this.props.updateProfileSetting(payload)
         }
      }
      catch (error) {
         logger(error);
      }
   }
   render() {
      const { profileData } = this.props;
      const { isLoading } = profileData
      const { errors, firstName, lastName, email } = this.state;
      return (
         <Card className={"card_align"}>
            <CardHeader>
               <strong>Profile Setting</strong>
            </CardHeader>
            <CardBody>
               <Row>
                  <Col lg="12" className={"custom-form-modal"}>
                     <Form onSubmit={this.handleSubmit}>
                        <FormGroup>
                           <Label htmlFor={"First Name"} className="customer-modal-text-style">
                              First Name<span className="asteric">*</span>
                           </Label>
                           <div className="input-block">
                              <Input
                                 type="text"
                                 value={firstName || ''}
                                 name="firstName"
                                 placeholder="First Name"
                                 onChange={this.handleInputChange}
                                 invalid={errors.firstName ? true : false}
                              />
                              <FormFeedback>
                                 {errors.firstName ? errors.firstName : null}
                              </FormFeedback>
                           </div>
                        </FormGroup>
                        <FormGroup>
                           <Label htmlFor={"old password"} className="customer-modal-text-style">
                              Last Name <span className="asteric">*</span>
                           </Label>
                           <div className="input-block">
                              <Input
                                 type="text"
                                 placeholder="Last Name"
                                 onChange={this.handleInputChange}
                                 value={lastName || ''}
                                 name="lastName"
                                 invalid={errors.lastName ? true : false}
                              />
                              <FormFeedback>
                                 {errors.lastName ? errors.lastName : null}
                              </FormFeedback>
                           </div>
                        </FormGroup>
                        <FormGroup>
                           <Label htmlFor={"old password"} className="customer-modal-text-style">
                              Email<span className="asteric">*</span>
                           </Label>
                           <div className="input-block ">
                              <Input
                                 type="text"
                                 placeholder="Email"
                                 onChange={this.handleInputChange}
                                 value={email || ''}
                                 name="email"
                                 disabled
                                 invalid={errors.email ? true : false}
                              />
                              <FormFeedback>
                                 {errors.email ? errors.email : null}
                              </FormFeedback>
                           </div>
                        </FormGroup>
                        <Row className="justify-content-center row">
                           <Col lg="4" md="4" sm="3" xs="2">
                              <FormGroup>
                                 <div className="input-block">
                                    <Button
                                       color="primary"
                                       className="btn-theme"
                                       type="submit"
                                       block
                                       onClick={this.handleSubmit}
                                    >
                                       {isLoading ? (<><Loader /> <span>Update</span></>) : "Update"}
                                    </Button>
                                 </div>
                              </FormGroup>
                           </Col>
                        </Row>
                     </Form>
                  </Col>
               </Row>
            </CardBody>
         </Card>
      )
   }
}

export default GenralSettings