import React, { Component } from "react";
import {
   Row,
   Col,
   Form,
   Input,
   FormGroup,
   FormFeedback,
   Button,
   Label,
   Card,
   CardHeader,
   CardBody
} from "reactstrap";
import { logger } from "../../../Helpers/Logger";
import Validator from "js-object-validation";
import { changePasswordValidation, changePasswordValidationMessages } from "../../../validations/profile.js";
import Loader from "../../../containers/Loader/Loader";

class UpdatePassword extends Component {
   constructor(props) {
      super(props);
      this.state = {
         errors: {},
         oldPassword: "",
         newPassword: "",
         confirmPassword: "",
         validErrors: {},
         isLoading: false
      };
   }
   componentDidUpdate = ({ profileData }) => {
      if (this.props.profileData.isSuccess !== profileData.isSuccess) {
         this.setState({
            oldPassword: "",
            newPassword: "",
            confirmPassword: "",
         })
      }
   };
   handleInputChange = e => {
      const { target } = e;
      const { name, value } = target;
      this.setState({
         [name]: value,
         errors: {
            ...this.state.errors,
            [name]: null
         }
      });
   };
   handleSubmit = e => {
      e.preventDefault();
      try {
         let validErrors = {};
         let hasErrors = false;
         const {
            oldPassword,
            newPassword,
            confirmPassword,
         } = this.state;

         const validData = {
            oldPassword:oldPassword.trim(),
            newPassword:newPassword.trim(),
            confirmPassword:confirmPassword.trim()
         };
         const payload = {
            oldPassword,
            newPassword,
         };
         const { isValid, errors } = Validator(
            validData,
            changePasswordValidation,
            changePasswordValidationMessages
         );
         if (!isValid || (!isValid && hasErrors) || hasErrors) {
            this.setState({
               validErrors,
               errors
            });
            return;
         }
         else {
            this.setState({ isLoading: true });
            this.props.updatePassword(payload)
         }
      }
      catch (error) {
         logger(error);
      }

   }

   render() {
      const { profileData } = this.props;
      const { isLoadingPass } = profileData;
      const { errors, oldPassword, newPassword, confirmPassword, } = this.state;
      return (
         <Card className={"card_align"}>
            <CardHeader>
               <strong>Change Password</strong>
            </CardHeader>
            <CardBody>
               <Row>
                  <Col lg="12" className={"custom-form-modal"}>
                     <Form onSubmit={this.handleSubmit}>
                        <FormGroup>
                           <Label htmlFor={"old password"} className="customer-modal-text-style">
                              Old Password <span className="asteric">*</span>
                           </Label>
                           <div className="input-block">
                              <Input
                                 type="password"
                                 placeholder="Old Password"
                                 onChange={this.handleInputChange}
                                 value={oldPassword || ''}
                                 name="oldPassword"
                                 invalid={errors.oldPassword ? true : false}
                              />
                              <FormFeedback>
                                 {errors.oldPassword ? errors.oldPassword : null}
                              </FormFeedback>
                           </div>
                        </FormGroup>
                        <FormGroup>
                           <Label htmlFor={"old password"} className="customer-modal-text-style">
                              New Password <span className="asteric">*</span>
                           </Label>
                           <div className="input-block">
                              <Input
                                 type="password"
                                 placeholder="New Password"
                                 onChange={this.handleInputChange}
                                 value={newPassword || ''}
                                 name="newPassword"
                                 invalid={errors.newPassword ? true : false}
                              />
                              <FormFeedback>
                                 {errors.newPassword ? errors.newPassword : null}
                              </FormFeedback>
                           </div>
                        </FormGroup>
                        <FormGroup>
                           <Label htmlFor={"old password"} className="customer-modal-text-style">
                              Confirm Password <span className="asteric">*</span>
                           </Label>
                           <div className="input-block">
                              <Input
                                 type="password"
                                 placeholder="Confirm Password"
                                 name="confirmPassword"
                                 onChange={this.handleInputChange}
                                 value={confirmPassword || ''}

                                 invalid={errors.confirmPassword ? true : false}
                              />
                              <FormFeedback>
                                 {errors.confirmPassword ? errors.confirmPassword : null}
                              </FormFeedback>
                           </div>
                        </FormGroup>
                        <Row className="justify-content-center row">
                           <Col lg="4" md="4" sm="3" xs="2">
                              <FormGroup>
                                 <div className="input-block">
                                    <Button
                                       color="primary"
                                       className="btn-theme"
                                       type="submit"
                                       block
                                       onClick={this.handleSubmit}
                                    >
                                       {isLoadingPass ? (<><Loader /> <span>Update</span></>) : "Update"}
                                    </Button>
                                 </div>
                              </FormGroup>
                           </Col>
                        </Row>
                     </Form>
                  </Col>
               </Row>
            </CardBody>
         </Card>
      )
   }
}

export default UpdatePassword